import React, { Component } from "react"
import { AiOutlineClockCircle } from "react-icons/ai"
import { CgNotes } from "react-icons/cg"
import styled from "styled-components"
import TinyColor from "tinycolor2"
import DefaultAvatar from "src/assets/images/avatar.jpg"

const ColorDefault = "rgb(46, 43, 239)"
class MUICard_Task extends Component {
  getLighten38 = color => {
    let _c = TinyColor(color).lighten(38).toString()
    return _c
  }

  render() {
    const { data = {} } = this.props
    const cardColor = data.CardColor || ColorDefault
    const lighten = this.getLighten38(cardColor)
    return (
      <WrapLayout cardColor={cardColor}>
        <div className="project-type-icon">
          <CgNotes size={40} color="#fff" />
        </div>
        <div className="project-name">{data.ProjectName}</div>
        <div className="task-name">{data.Name}</div>
        <div className="wrap-assigner">
          {data.AssignTo.length > 2 ? (
            <>
              {data.AssignTo?.slice(0, 2)?.map((assigner, i) => {
                return (
                  <div className="assigner-item" key={i}>
                    <img
                      className="assigner-img"
                      src={assigner.Avatar || DefaultAvatar}
                      alt="assigner"
                    />
                  </div>
                )
              })}
              <div className="assigner-item assigner-more">
                +{data.AssignTo.length - 2}
              </div>
            </>
          ) : (
            data.AssignTo?.map((assigner, i) => {
              return (
                <div className="assigner-item" key={i}>
                  <img
                    className="assigner-img"
                    src={assigner.Avatar || DefaultAvatar}
                    alt="assigner"
                  />
                </div>
              )
            })
          )}
        </div>
        <div className="progress-wrap">
          <div className="progress-header">
            <div className="progress-title">{data.Status}</div>
            <div className="progress-percent">
              {data.Percent ? data.Percent : 0}%
            </div>
          </div>
          <div className="progress-volume">
            <div
              className="progress-finished"
              style={{ width: data.Percent + "%" }}
            ></div>
          </div>
        </div>
        <div className="remain-wrap">
          <div className="content-remain" style={{ backgroundColor: lighten }}>
            <AiOutlineClockCircle size={20} />
            <span className="title-remain">2 Week left</span>
          </div>
        </div>
      </WrapLayout>
    )
  }
}

const WrapLayout = styled.div`
  min-height: 150px;
  background: #fff;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  border-radius: 10px;
  padding: 2rem 1rem 1rem;
  position: relative;
  & .project-type-icon {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: ${props => props.cardColor};
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
      rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  & .project-name {
    text-align: center;
    font-weight: 700;
    font-size: 18px;
  }
  & .task-name {
    text-align: center;
    color: #887c7c;
    font-size: 16px;
  }
  & .wrap-assigner {
    margin: 1rem 0;
    display: flex;
    justify-content: center;
    margin-left: 10px;
    min-height: 50px;
    & .assigner-item {
      margin-left: -10px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      overflow: hidden;
      border: 1px solid #d9d9d9;
      &.assigner-more {
        background: #d9d9d9;
        color: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
      }
    }
    & .assigner-img {
      width: 100%;
      height: 100%;
    }
  }
  & .progress-header {
    display: flex;
    justify-content: space-between;
    color: #000;
    & .progress-title {
      font-weight: 700;
    }
  }
  & .progress-volume {
    width: 100%;
    height: 10px;
    background: #ececec;
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    margin: 0.5rem 0;
    & .progress-finished {
      height: 100%;
      background: ${props => props.cardColor};
    }
  }
  & .remain-wrap {
    display: flex;
    justify-content: flex-end;
    & .content-remain {
      color: ${props => props.cardColor};
      padding: 0.5rem;
      display: flex;
      align-items: center;
      border-radius: 5px;
      & .title-remain {
        margin-left: 0.5rem;
      }
    }
  }
`
export default MUICard_Task
